<template>
  <div class="page unbind-google-auth">
    <div class="tips">
      输入动态验证码以验证身份
    </div>
    <van-password-input :value="password"
                        :focused="showKeyboard"
                        @focus="showKeyboard = true">

    </van-password-input>
    <van-number-keyboard v-model="password"
                         :show="showKeyboard"
                         @blur="showKeyboard = false">

    </van-number-keyboard>
    <van-button class="block primary" @click="unBindGoogleAuthenticator">
      解绑
    </van-button>
  </div>
</template>

<script>
import {unBindGoogleAuthenticator} from "@/apis/user";
import {NumberKeyboard, PasswordInput} from "vant";

export default {
  components: {
    [NumberKeyboard.name]: NumberKeyboard,
    [PasswordInput.name]: PasswordInput
  },
  data() {
    return {
      password: '',
      showKeyboard: false
    }
  },
  watch: {
    password(password) {
      if (password.length === 6) {
        this.showKeyboard = false
      }
    }
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back"]);
  },
  methods: {
    async unBindGoogleAuthenticator() {
      if (this.password.length !== 6) return this.$toast.fail("请输入google验证码")
      const {ok, msg} = await unBindGoogleAuthenticator({safeCode: this.password})
      if (ok) {
        this.$toast.success(msg);
        await this.$root.fetchUserInfo()
        this.$router.back()
      } else {
        this.$toast.fail(msg)
      }
    }
  }
}
</script>


<style lang="scss">
.unbind-google-auth {
  .tips {
    font-size: 12px;
    text-align: center;
    margin: 20px 25px 0;
  }

  .van-password-input {
    margin-top: 40px;

    *::after {
      border-color: black;
    }
  }

  .block.primary {
    margin-top: 250px;
  }
}
</style>
